/* OperadoraAtencionCliente.css */
.operadora-container {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 999;
}

.operadora-icon {
  background-color: #007bff;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; /* Oculta cualquier parte de la imagen que sobresalga del círculo */
}

.operadora-icon img {
  width: 100%; /* Asegura que la imagen se ajuste completamente dentro del círculo */
  height: 100%;
  object-fit: cover; /* Hace que la imagen se ajuste dentro del contenedor manteniendo su relación de aspecto */
}


  